/* styles.css */
.container {
    padding: 20px;
    margin-top: 20px;
  }
  
  .skill {
    margin-bottom: 10px;
  }
  
  .progress-bar {
    width: 100%;
  }
  
  /* Optional: Add colors to the progress bar based on proficiency level */
  .skill .MuiLinearProgress-barColorPrimary {
    background-color: #4caf50; /* Green */
  }
  
  .skill .MuiLinearProgress-barColorSecondary {
    background-color: #ff9800; /* Orange */
  }
  
  /* Optional: Style the text above the progress bar */
  .skill .MuiTypography-root {
    margin-bottom: 5px;
  }
  .background-container1 {
    position: relative;
    background-image: url('../assts/pxfuel1.jpg');
    background-size: cover;
    background-position: center;
    height: 100vh;
    overflow: hidden;
  }
  /* Experience.css */

.work-experience-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.experience-item {
  position: relative;
  width: 100%;
  max-width: 600px; /* Adjust the max-width based on your design */
  margin-bottom: 50px;
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.line-container {
  position: absolute;
  top: 50%;
  width: 100%;
  display: flex;
  align-items: center;
}

.line {
  flex-grow: 1;
  height: 2px;
  background-color: #000;
}

.dot {
  width: 10px;
  height: 10px;
  background-color: #000;
  border-radius: 50%;
  margin: 0 10px;
}

.text-container {
  display: flex;
  align-items: center;
  margin: 0 10px;
}

.text {
  font-size: 18px;
  margin: 0 10px;
  color: #000; /* Adjust the text color */
}

/* Add your own styles as needed */
 