/* Home.css */

/* Main Content */
.container {
    text-align: center;
    margin-top: 50px;
  }
  
  h4 {
    color: #333;
  }
  
  .subtitle {
    color: #555;
  }
  
  /* About Me Button */
  .about-button {
    margin-top: 20px;
  }
  
  /* Social Media Icons */
  .social-icons {
    margin-top: 20px;
  
    .MuiSvgIcon-root {
      font-size: 2rem;
      margin: 0 10px;
      color: #333;
    }
  }
  
  /* Profile Picture */
  .profile-picture {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    overflow: hidden;
    margin: 20px auto;
    background-color: lightgray;
  
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  
  /* Footer */
  .footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 10px;
    background-color: #333;
    color: #fff;
    text-align: center;
  }
 
  .resume{
    position: absolute;
    top: 0;
    right: 0;
    margin: 1rem;
    margin-top: 40px;
  }
  .background-container {
    position: relative;
    background-image: url('../assts/pxfuel.jpg');
    background-size: cover;
    background-position: center;
    height: 100vh;
    overflow: hidden;
  }
  .about{
    margin-top: 300px;
  }
  .header {
    position: sticky;
    top: 0;
    z-index: 1000;
    background-color: #ffffff;
}

/* Add a fixed height to the header */
.header .MuiToolbar-root {
  height: 60px; /* Adjust the height as needed */
}

