.app-container {
    position: relative;
    padding: 20px;
  }
  
  .navbar {
    position: absolute;
    top: 20px;
    right: 20px;
  }
  
  .navbar ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .navbar li {
    display: inline-block;
    margin-right: 10px;
  }
  
  .navbar a {
    text-decoration: none;
    color: #333333;
    font-weight: bold;
  }
  .header {
    background-color: #333;
  }
  
  .portfolio-link {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
  }
  
  .nav-list {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .nav-list li {
    display: inline-block;
    margin-right: 10px;
  }
  
  .nav-list a {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
  }
  .footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 10px;
    background-color: #333;
    color: white;
    text-align: center;
  }